import { Injectable } from '@angular/core'
import { HttpClient } from '@angular/common/http';
import { HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map, tap } from 'rxjs/operators';

@Injectable()
export class ArticoliService {



    constructor(private httpService: HttpClient) {

    
    }


    getArticoli(): Observable<any[]> {
        return this.httpService.get('./assets/giacenza.json').pipe(
            map((data:any[])=>{
                return data.sort((a, b) => (a.descrizione > b.descrizione) ? 1 : -1)
            })
        );
    }

    search(filterString: string): Observable<any[]> {

        return this.getArticoli().pipe(
          map(art => {
            if (filterString == null || filterString == '') return [];
            let str = filterString.toLowerCase();
            return art.filter(a => a.codice.toLowerCase().includes(str) ||
              a.descrizione.toLowerCase().includes(str))
          })
        )
    
      }

}