import { Component, OnInit } from '@angular/core';

import { OrariService } from '../services/orari.service'
import { Observable} from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html'
})
export class HomeComponent implements OnInit {

  orarioApri:string='15:00'
  orarioChiudi:string='18:00'
  
  dateApertura$: Observable<any[]> ;

  constructor(private orariService:OrariService) { 

    

  }
 
  ngOnInit() {
    this.dateApertura$=this.orariService.getOrari()
  }

}
